<template>
  <div class="content-r">

    <div class="content-r-wrap" v-if="isshow">
      <input type="text" placeholder="输入邮箱" class="input" v-model="formData.email">
      <input type="password" placeholder="输入密码" class="input" v-model="formData.password" @keyup.enter="hendlelogin">
      <el-button class="login btn" type="primary" @click="hendlelogin">登录</el-button>
      <br>
      <el-button class="btn" @click="hendleRegister">注册</el-button>
    </div>


    <div class="content-r-wrap" v-else>
      <div><img :src="formData.avatar" class="image"></div>
      <div class="username">用户名：{{formData.username}}</div>
      <div class="email">邮箱：{{formData.email}}</div>
      <el-button @click="hendlelogout" class="logout-btn">退出登录</el-button>
    </div>

  </div>
</template>

<script>
    export default {
        name: "userbox",
      data(){
        return {
          formData:{
            email:'',
            password:'',
            avatar:'',
            username:''
          },
          isshow:true
        }
      },
      methods:{
        hendleRegister(){
          this.$router.push('/register')
        },
        hendlelogin(){
          this.$axios.post('/login',this.formData).then( res => {
            if(res.code === 200){
              this.formData = res.userdata;
              this.$store.commit('CHANGE_TOKEN',res.token);
              this.$message.success('登陆成功')
              this.isshow = false
            }
            else if(res.code === 400){
              this.$message('邮箱密码不正确，请重新输入')
            }
          })
        },
        hendlelogout(){
          this.$store.commit('CHANGE_TOKEN',null);
          this.$message.success('退出登录成功')
          localStorage.clear()
          this.isshow = true

        },
        show(){
          if(this.$store.state.token){
            let token = this.$store.state.token
            this.isshow = false
            this.$axios.get('/userdetail',{token}).then( res => {
              if( res.code === 200){
                this.formData = res.data
              }
            })
          }
        }

      },
      created(){
        this.show()
      },
      watch:{
        // isshow(){
        //   return this.$store.state.token
        // }
      }
    }
</script>

<style scoped lang="scss">

  .content-r {
    width: 360px;
    height: 340px;
    background: #f1f1f1;
    border: 1px solid #f2f2f2;
    border-radius: 5px;

  .content-r-wrap {
    width: 300px;
    margin: 30px auto 10px;
    box-sizing: border-box;
    text-align: center;
  }

  .input {
    width: 260px;
    height: 40px;
    margin-top: 20px;
  }

  .btn {
    width: 264px;
    margin-top: 20px;
  }

  .image{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 20px;
  }

  .username{
    font-size: 15px;
    color: #e6a23c;
  }
  .email{
    font-size: 15px;
    margin-top: 10px;
  }
  .logout-btn{
    width: 300px;
    margin: 20px auto;
    background: #ff4d51;
    color: #ffffff;
  }

  }
</style>



